@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
:root {
    --light-blue: #eef7fa;
    --blue: rgb(70, 120, 160);

    --smoke: #f0f0f0;
    --gray: #e8e8e8;
    --dark-gray: #e0e0e0;
    --black: #1a1a1a;

    --font1: 'Lato', sans-serif;
    --font2: 'Poppins', sans-serif;

    --header-height: 45px;
    --footer-height: 65px;
}

body,
html {
    background-color: #1a1a1a;
    background-color: var(--black);
    margin: 0;
    padding: 0;
    max-width: 100%;
    font-family: 'Lato', sans-serif;
    font-family: var(--font1);
    scroll-behavior: smooth;
}

a {
    color: inherit;
}
a:hover {
    text-decoration: none;
}

.button {
    display: inline-block;
    background-color: #1a1a1a;
    background-color: var(--black);
    border: 1px black solid;
    color: white;
    transition: all 0.1s ease-in-out;
}
.button:focus {
    outline: none;
}
.button:hover {
    color: white;
    transform: scale(1.1);
}

.tag {
    display: inline-block;
    font-size: 12px;
    border-radius: 8px;
    padding: 3px 6px;
    margin: 3px 6px 3px 0;
    cursor: default;
}

.languageTag {
    background-color: lightsalmon;
}

.toolTag {
    background-color: lightblue;
}

.blueLink {
    color: blue !important;
}

@media (max-width: 575px) {
    .tag {
        font-size: 10px;
        border-radius: 8px;
        padding: 1px 6px;
    }
}


.loadingScreen {
    background-color: var(--light-blue);
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.hidden {
    display: none;
}

.home {
    color: var(--black);
    background-color: white;
}

.home .landingScreen {
    height: 100vh;
    width: 100%;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.home .backgroundVideoWrap {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    overflow: hidden;
}

.home .backgroundVideoWrap video {
    min-width: 100%;
    min-height: 100%;
}

.home .videoOverlay {
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    z-index: 1;
    background: white;
    opacity: 0.3;
}

.home .landingContent {
    padding-bottom: 110px;
    width: 70%;
    z-index: 2;
}

.home .landingTitle {
    font-family: var(--font2);
    font-size: 90px;
    font-weight: bolder;
}

.home .landingSubtitle {
    padding-top: 5px;
    font-family: var(--font2);
    font-size: 30px;
}

.home .landingArrow {
    font-size: 50px;
    -webkit-animation: MoveUpDown 1s linear infinite;
            animation: MoveUpDown 1s linear infinite;
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    z-index: 2;
}

.home .landingArrow button {
    color: var(--black);
    background: none;
    border: none;
}

.home .landingArrow button:focus {
    outline: none;
}

@-webkit-keyframes MoveUpDown {
    0%,
    100% {
        bottom: 120px;
    }
    50% {
        bottom: 130px;
    }
}

@keyframes MoveUpDown {
    0%,
    100% {
        bottom: 120px;
    }
    50% {
        bottom: 130px;
    }
}

@media (max-width: 1199px) {
}

@media (max-width: 991px) {
}

@media (max-width: 767px) {
    .home .landingTitle {
        font-size: 60px;
    }
}

@media (max-width: 575px) {
    .home .landingContent {
        width: 90%;
    }

    .home .landingTitle {
        font-size: 48px;
    }

    .home .landingSubtitle {
        font-size: 22px;
    }
}

@media (max-height: 715px) {
    .home .landingContent {
        padding-bottom: 0;
    }

    .home .landingArrow {
        visibility: hidden;
    }
}

.skills .skillRow {
    padding-bottom: 10px;
}

.skills .skillCategory {
    font-weight: bold;
    border-bottom: 1px solid var(--black);
    margin-bottom: 10px;
}

.skills .skillBlock {
    text-align: center;
    font-size: 12px;
    padding-bottom: 5px;
}

.skills .skillLink {
    text-decoration: none;
    color: inherit;
}

.skills .skillIcon {
    width: 30%;
    padding-bottom: 5px;
}

@media (max-width: 991px) {
    .skills .skillIcon {
        width: 50%;
    }
}

.about {
    padding: 50px 0 20px 0;
    background-color: var(--gray);
    color: var(--black);
    font-size: 20px;
    min-height: calc(100vh - var(--header-height) - var(--footer-height));
}

.about .container {
    display: flex;
}

/* Sidenav */
.about .sideNav {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    margin-right: 20px;
}

.about .sideNavButton {
    cursor: pointer;
    text-align: right;
    padding: 10px 10px;
    border-right: 4px solid var(--dark-gray);
}
.about .sideNavButton:hover {
    background-color: var(--dark-gray);
}
.about .sideNavButtonActive {
    color: var(--blue);
    font-weight: bold;
    border-right: 4px solid var(--blue);
}

.about .content {
    flex: 1 1; /* Width is 100% minus sideNav width */
}

/* Intro */

.about .portraitRow {
    padding: 0 15px;
}
.about .portraitRow > div {
    padding: 0;
}

.about .portrait {
    width: 100%;
    padding-bottom: 20px;
}

.about .aboutIntro {
    font-size: 22px;
}

/* Showcase */

.about .showcaseSection {
    text-align: center;
    margin: 0px 0 55px 0;
}

.about .experienceImage,
.about .projectsImage {
    width: 75%;
}

.about .projectsImage {
    border: 1px solid var(--black);
}

.about .showcaseButton {
    margin-top: 25px;
    font-size: 22px;
    padding: 3px 15px;
    border-radius: 5px;
}

.about .aboutQuote {
    padding: 20px 5%;
    margin: 45px auto 20px auto;
    font-size: 20px;
    border-top: 3px solid var(--blue);
    border-bottom: 3px solid var(--blue);
    width: 90%;
}
.about .aboutQuote .fa-quote-left {
    margin-right: 12px;
}
.about .aboutQuote .fa-quote-right {
    margin-left: 12px;
}
.about .aboutQuote .aboutQuoteName {
    text-align: right;
    padding-top: 5px;
    font-size: 18px;
    font-style: italic;
}
.about .aboutQuote .aboutQuoteName svg {
    margin-left: 5px;
}

@media (max-width: 991px) {
    .about {
        font-size: 18px;
    }

    /* Make sidenav horizontal */
    .about .container {
        display: block;
    }
    .about .content {
        flex: none;
    }
    .about .sideNav {
        margin: 0 0 10px 0;
    }
    .about .sideNavButton {
        display: inline-block;
        text-align: left;
        padding: 5px 10px;
        border-right: none;
        border-bottom: 4px solid var(--dark-gray);
    }
    .about .sideNavButtonActive {
        border-right: none;
        border-bottom: 4px solid var(--blue);
    }

    /* Intro */
    .about .portraitRow > div {
        padding: 0;
    }

    .about .aboutIntro {
        font-size: 18px;
    }

    /* Showcase */
    .about .showcaseSection {
        margin: 20px 0;
    }

    .about .aboutQuote {
        width: 100%;
    }
}

.experience {
    margin-top: var(--header-height);
    background-color: white;
    padding: 10px 0;
    color: var(--black);
    font-size: 20px;
}

.experience .experienceRow {
    background-color: var(--smoke);
    padding: 20px;
    margin: 10px 0 20px 0;
}

.experience .experienceRow:hover {
    background-color: var(--light-blue);
}

.experience .experienceRowLeft {
    min-height: 200px;
}

.experience .experienceRowTitle {
    font-size: 30px;
    text-decoration: none !important;
    color: inherit;
}
.experience .experienceRowTitle:hover {
    color: inherit;
}

.experience .experienceRowRight {
    position: relative;
}

.experience .experienceImage {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    max-width: 65%;
    max-height: 100%;
}

@media (max-width: 991px) {
    .experience .experienceImage {
        max-width: 75%;
        max-height: 100%;
    }
}

@media (max-width: 767px) {
    .experience {
        font-size: 17px;
    }

    /* Remove vertical centering */
    .experience .experienceRow {
        text-align: center;
    }

    .experience .experienceImage {
        position: relative;
        left: auto;
        top: auto;
        transform: none;

        max-height: 120px;
    }
}

@media (max-width: 575px) {
    .experience {
        font-size: 15px;
    }
    .experience .experienceRowTitle {
        font-size: 25px;
    }
}

.ReactModal__Overlay {
    z-index: 20;
}

.ReactModal__Content {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.imageCarousel {
    overflow-y: hidden;
    overflow-x: hidden;
}

.imageCarousel .projectImage {
    max-width: 100%;
    max-height: 85vh;
    margin-left: auto;
    margin-right: auto;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.imageCarousel .slideControl {
    position: absolute;
    outline: none;
    padding: 15px;
    border: none;
    background-color: rgb(0, 0, 0, 0.4);
    color: white;
    font-size: 50px;
    top: 50%;
    transform: translateY(-50%);
}

.imageCarousel .previousButton {
    left: 3vw;
}

.imageCarousel .nextButton {
    right: 3vw;
}

.imageCarousel .imagePage {
    position: absolute;
    padding: 3px 10px;
    background-color: rgb(0, 0, 0, 0.5);
    border-radius: 15%;
    color: white;
    right: 3vw;
    top: 3vh;
}

.projects {
    margin-top: var(--header-height);
    padding: 10px 0 0px 0;
    background-color: white;
    color: var(--black);
    font-size: 20px;
}

.projects .projectRow {
    background-color: var(--smoke);
    padding: 20px;
    margin: 10px 0 20px 0;
}

.projects .projectRow:hover {
    background-color: var(--light-blue);
}

.projects .projectRowTitle {
    font-size: 30px;
}

.projects .projectImageAndButtons {
    width: 100%;
    max-height: 100%;
    text-align: center;
}

.projects .projectImage {
    max-width: 80%;
    cursor: pointer;
    border: 2px solid var(--blue);
}

.projects .projectButtons {
    margin-top: 10px;
}

.projects .projectButtons button {
    font-size: 15px;
    padding: 5px 15px;
    margin: 0 3%;
    border-radius: 5px;
    min-width: 22%;
}

.projects .moreProjects {
    background-color: var(--smoke);
    padding: 20px;
    margin: 20px 0 0 0;
    text-align: center;
}

@media (max-width: 991px) {
    /* Vertically Center image and buttons at this screen width */
    .projects .projectRowRight {
        position: relative;
    }

    .projects .projectImageAndButtons {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    .projects .projectImage {
        max-width: 90%;
    }
}

@media (max-width: 767px) {
    .projects {
        font-size: 17px;
    }

    /* Remove vertical centering */
    .projects .projectRow {
        text-align: center;
    }

    .projects .projectImageAndButtons {
        position: relative;
        left: auto;
        top: auto;
        transform: none;
    }

    .projects .projectImage {
        max-width: 70%;
    }
}

@media (max-width: 575px) {
    .projects {
        font-size: 15px;
    }
    .projects .projectRowTitle {
        font-size: 25px;
    }
    .projects .projectImage {
        max-width: 90%;
    }
}

.footer {
    height: var(--footer-height);
    display: flex;
    align-items: center;
    background-color: var(--black);
    color: var(--dark-gray);
    text-align: center;
    z-index: 3;
}

.footer .footerIcon {
    display: inline-block;
    padding: 0 35px;
    font-size: 30px;
    transition: all 0.1s ease-in-out;
}

.footer .footerIcon:hover {
    color: var(--blue);
    transform: scale(1.2);
}

.footer .footerCopyright {
    padding-top: 10px;
}

@media (max-width: 575px) {
    .footer .footerIcon {
        padding: 0 24px;
        font-size: 25px;
    }
}

.header {
    position: fixed;
    top: 0;
    width: 100%;
    font-size: 16px;
    background-color: var(--black);
    list-style-type: none;
    height: var(--header-height);
    z-index: 3;
    border-bottom: 3px var(--blue) solid;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.header .headerContainer {
    position: absolute;
    left: 0;
    right: 0;
    top: 55%;
    transform: translateY(-50%);
}

.header .logo {
    font-family: var(--font2);
    float: left;
    font-weight: bolder;
}

.header .headerRight {
    margin: 0;
    padding: 0;
    text-align: right;
}

.header .headerRight li {
    margin-left: 20px;
}

.header .navItem {
    display: inline;
}

.header .navItem a {
    display: inline-block;
    color: white;
    text-decoration: none;
    transition: all 0.1s ease-in-out;
}

.header .navItem a:hover {
    transform: scale(1.1);
}

.header .navItemActive {
    color: var(--blue) !important;
    font-weight: bolder;
}

